import Vue from 'vue'
import VueRouter from 'vue-router'

// 导入登录路由
import Login from '../views/Login'
import Home from '../views/Home'
import Welcome from '../views/Welcome'
import Users from '@/views/User/Users.vue'
import Rights from '@/views/Power/Rights.vue'
import Roles from '@/views/Power/Roles.vue'
import Category from '@/views/Goods/Category.vue'
import Params from '@/views/Goods/Params.vue'
import GoodsList from '@/views/Goods/GoodsList.vue'
import AddGoods from '@/views/Goods/AddGoods.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/login' }, // 路由重定向, 页面一打开就是登录页面
  // 配置登录组件规则
  { path: '/login', component: Login },
  {
    path: '/home',
    component: Home,
    redirect: '/welcome',
    children: [
      { path: '/welcome', component: Welcome },
      { path: '/users', component: Users },
      { path: '/rights', component: Rights },
      { path: '/roles', component: Roles },
      { path: '/categories', component: Category },
      { path: '/params', component: Params },
      { path: '/goods', component: GoodsList },
      { path: '/goods/add', component: AddGoods }
    ]
  }
]

const router = new VueRouter({
  routes
})

// 路由导航守卫
// 前置路由守卫  就是在正式进入某一个路由之前进行判断
router.beforeEach((to, from, next) => {
  // to 表示将要跳转的地址
  // from 从哪个地址跳转过来
  // next 是一个函数, 表示放行, 执行下一步操作
  //     next() - 放行   next('/login') - 强制跳转

  // 如果用户访问的是登录页面, 直接放行
  if (to.path === '/login') return next()

  // 获取本地存储中的token字段, 判断token, 不存在、过期强制跳转到登录页
  const tokenStr = window.sessionStorage.getItem('token')
  if (!tokenStr) return next('/login')

  next()
})

// vue router连续点击多次路由报错解决方法
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}

export default router
