<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 提示信息 -->
      <el-alert title="添加商品信息" type="info" center show-icon :closable="false"></el-alert>

      <!-- 步骤条 -->
      <el-steps :active="+activeIndex" align-center finish-status="success">
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>

      <!-- tab栏区域 -->
      <el-form :model="addForm" ref="addFormRef" :rules="addFormRules" size="medium">
        <el-tabs
          tab-position="left"
          v-model="activeIndex"
          :before-leave="beforeTabLeave"
          @tab-click="tabClicked"
        >
          <!-- 基本信息栏目 -->
          <el-tab-pane label="基本信息" name="0">
            <!-- 商品名称 -->
            <el-form-item label="商品名称:" prop="goods_name" label-width="100px">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <!-- 商品价格 -->
            <el-form-item label="商品价格:" prop="goods_price" label-width="100px">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <!-- 商品重量 -->
            <el-form-item label="商品重量:" prop="goods_weight" label-width="100px">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <!-- 商品数量 -->
            <el-form-item label="商品数量:" prop="goods_number" label-width="100px">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <!-- 商品分类 -->
            <el-form-item label="商品分类:" prop="goods_cat" label-width="100px">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="catelist"
                :props="cateProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <el-form-item
              :key="item.attr_id"
              v-for="item in manyTabData"
              :label="item.attr_name + ':' "
              label-width="60px"
            >
              <el-checkbox-group v-model="item.attr_vals" size="mini">
                <el-checkbox
                  :key="index"
                  v-for="(item2, index) in item.attr_vals"
                  :label="item2"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <el-form-item
              :key="item.attr_id"
              v-for="item in onlyTabData"
              :label="item.attr_name + ':' "
              label-width="160px"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <!-- action 图片上传到后台的API地址 -->
            <el-upload
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headersObj"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <!-- 富文本编辑器插件 -->
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <!-- 添加商品按钮 -->
            <el-button type="primary" class="addBtn" @click="addGoods">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!-- 图片预览 -->
    <el-dialog title="图片预览" :visible.sync="previewVisible" width="50%">
      <img :src="previewPath" alt width="100%" />
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  name: 'AddGoods',
  data() {
    return {
      // 控制当前步骤的激活状态
      activeIndex: '0',
      // 添加商品的表单数据
      addForm: {
        goods_name: '', // 商品名称
        goods_price: 0, // 商品价格
        goods_weight: 0, // 商品重量
        goods_number: 0, // 商品数量
        goods_cat: [], // 级联选择器选中的分类数组
        pics: [], // 图片的数组
        goods_introduce: '', // 商品内容
        attrs: [] // 商品的参数
      },
      // 添加商品的表单验证规则对象
      addFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '请选择商品分类', trigger: 'blur' }
        ]
      },
      catelist: [], // 商品分类列表数据
      // 级联选择器的配置项
      cateProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      manyTabData: [], // 动态参数数据
      onlyTabData: [], // 静态属性数据
      // 图片上传的URL地址
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // upload组件配置 headers请求头对象
      headersObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      previewPath: '', // 预览的图片URL地址
      previewVisible: false // 控制图片预览对话框的显示与隐藏
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 获取商品分类列表
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.catelist = res.data
    },

    // 级联选择器选中项发生变化时触发
    handleChange() {
      // 只允许选择三级分类
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },

    // tab栏 切换之前会触发的一个函数
    beforeTabLeave(activeName, oldActiveName) {
      // activeName 将要切换到的栏目
      // oldActiveName 从哪个栏目切换来
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('请选择商品分类')
        return false // 这个函数内return false 会阻止切换
      }
    },

    // tab 被选中时触发
    async tabClicked() {
      // 证明选中的是动态参数面板
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'many' }
          }
        )
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        // 将attr_vals数据转成数组
        res.data.forEach(item => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(',')
        })

        this.manyTabData = res.data
      } else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: 'only' }
          }
        )
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.onlyTabData = res.data
      }
    },

    // 处理图片预览效果
    handlePreview(file) {
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },

    // 移除图片时触发
    handleRemove(file) {
      // 获取将要删除的图片的临时路径
      const filePath = file.response.data.tmp_path
      // 从pics 数组中, 找到这个图片相应的索引值
      const i = this.addForm.pics.findIndex(item => {
        return item.pic === filePath
      })
      // 调用splice()方法把相应的图片从pics数组中移除
      this.addForm.pics.splice(i, 1)
    },

    // 监听文件上传成功后的事件
    handleSuccess(response) {
      // 处理接口需要的数据格式
      // 1. 将图片临时地址处理成对象的形式
      const picInfo = { pic: response.data.tmp_path }
      // 2. 将这个对象push进pics数组中
      this.addForm.pics.push(picInfo)
    },

    // 添加商品
    addGoods() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return this.$message.error('请填写必要的表单项')

        // 执行添加商品的业务逻辑
        // const cloneForm = _.cloneDeep(this.addForm)
        // const cloneForm = JSON.parse(JSON.stringify(this.addForm)) // 反序列化
        const cloneForm = { ...this.addForm } // 展开运算符
        cloneForm.goods_cat = cloneForm.goods_cat.join(',')
        // 处理动态参数
        this.manyTabData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(',')
          }
          this.addForm.attrs.push(newInfo)
        })
        // 处理静态属性
        this.onlyTabData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          this.addForm.attrs.push(newInfo)
        })
        // 最终提交的是cloneForm而不是addForm
        cloneForm.attrs = this.addForm.attrs

        // 发送请求完成添加
        const { data: res } = await this.$http.post('goods', cloneForm)
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.$router.push('/goods')
      })
    }
  },

  computed: {
    // 获取三级分类id
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[this.addForm.goods_cat.length - 1]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .el-tabs__item {
  font-weight: normal;
}

.add-goods {
  margin-top: 30px;
}

.el-checkbox {
  margin: 0 8px 0 0 !important;
}

.addBtn {
  margin-top: 15px;
}
</style>
