  .avatar-box {
    width: 150px;
    height: 150px;
    margin: 0 auto;
    transform: translateY(-50%);
    padding: 10px;
    background-color: #fff;
    border: 1px solid #eee;
    border-radius: 50%;
    box-shadow: 0 0 10px #ddd;

    img {
      width: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }

  .login-form {
    width: 100%;
    padding: 0 20px;
    position: absolute;
    bottom: 0;

    .btns {
      float: right;
    }

    // 在实际开发中, 可能需要对子组件的样式进行调整
